<template>
  <div class="container">
    <div
      class="chat-list"
      v-bind:style="[ message.username!=username ? {'justify-content':'flex-start'}:{'justify-content':'flex-end'}]"
      v-for="message in messages"
    >
      <img v-if="message.username!=username" src="../assets/profile01.jpg" class="profile-img">
      <div
        class="chat-body"
        v-bind:style="[ message.username!=username ? {'align-items':'flex-start'}:{'align-items':'flex-end'}]"
      >
        <p class="username">{{ message.username }}</p>
        <p class="chat-content">{{ message.message }}</p>
      </div>
      <img v-if="message.username===username" src="../assets/profile02.jpg" class="profile-img">
    </div>
    <form class="form-input" action>
      <input class="input-text" type="text" v-model="message">
      <button class="input-submit" type="button" @click="submitMessage()">GO</button>
    </form>
  </div>
</template>

<script>
export default {
  name: "HelloWorld",
  data() {
    return {
      username: "Branson",
      data: {
        isUsername: true
      },
      message: "",
      messages: [
        {
          username: "Branson",
          message: "Hi"
        },
        {
          username: "Pandora",
          message: "?"
        }
      ]
    };
  },
  methods: {
    submitMessage() {
      let vm = this;
      vm.messages.push({
        username: vm.username,
        message: vm.message
      });
      vm.message='';
      console.log(vm.username);
    }
  }
  // mounted() {
  //   setTimeout(() => {
  //     this.$router.push("/myPage");
  //   }, 2000);
  // }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.container {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  background-color: darkcyan;
  height: 640px;
  overflow: hidden;
  .chat-list {
    display: flex;
    flex-direction: row;

    .profile-img {
    }
    .chat-body {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      padding: 5px;
      .username {
        font-size: 1.5em;
        font-weight: bold;
      }
      .chat-content {
        font-size: 1.3em;
      }
    }
  }
  .form-input {
    display: flex;
    flex-direction: row;
    height: 35px;
    border: 3px solid silver;
    .input-text {
      flex-grow: 10;
      height: 30px;
      text-align: right;
      font-size: 30px;
    }
    .input-submit {
      flex-grow: 1;
      height: 36px;
    }
  }
}
</style>



